.alert {
  position: absolute;
  /* top: 0; */
  right: 8px;
  width: 300px;
  height: 55px;
  background-color: rgb(224, 79, 103);
  color: whitesmoke;
  border-radius: 5px;
  font-size: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.alert__name {
  top: 0;
}
.alert__message {
  top: 65px;
}

.enter {
  opacity: 0;
}

.enterActive {
  opacity: 1;
  transition: all 250ms linear;
}

.exit {
  opacity: 1;
}

.exitActive {
  opacity: 0;
  transition: all 250ms linear;
}
